<template>
	<div>
		<div data-am-widget="header" class="am-header am-header-default am-no-layout ft-header2">
			<div class="am-header-left am-header-nav">
				<a href="javascript:history.back()" class="">
					<i class="am-icon-angle-left"></i>
				</a>
			</div>
			<h1 class="am-header-title"><a href="#title-link" class="">华航烟酒</a></h1>
			<div class="am-header-right am-header-nav">
				<button class="am-btn am-icon-bars" data-am-offcanvas="{target: '#doc-oc-demo2', effect: 'push'}"></button>
				<div id="doc-oc-demo2" class="am-offcanvas">
					<div class="am-offcanvas-bar">
						<ul class="am-menu-nav am-avg-sm-1">
							<li><router-link to="/" @click.native="refresh">首页</router-link></li>
							<li><router-link to="">关于我们</router-link></li>
							<li><router-link to="/categoryProduct" @click.native="refresh">产品中心</router-link></li>
							<li><router-link to="/discount" @click.native="refresh">优惠讯息</router-link></li>
							<li><router-link to="/news" @click.native="refresh">新闻资讯</router-link></li>
							<li><router-link to="">联系我们</router-link></li>
						</ul>
					</div>
				</div>
			</div>
		</div>
		<div style="height: 49px;"></div>
	</div>
</template>
<script>
export default {
	methods:{
		refresh:function(){
			this.$router.go(0)
		}
	}
}
</script>


