<template>
    <div>
		<!--图片轮播-->
        <div id="carousel-example-generic" class="carousel slide" data-ride="carousel">
			<!-- Indicators -->
			<ol class="carousel-indicators">
				<li data-target="#carousel-example-generic" data-slide-to="0" class="active"></li>
				<li data-target="#carousel-example-generic" data-slide-to="1"></li>
				<li data-target="#carousel-example-generic" data-slide-to="2"></li>
			</ol>
			<!-- Wrapper for slides -->
			<div class="carousel-inner" role="listbox">
				<div class="item active">
					<img src="../assets/img/banner/discount-banner1.jpg">
				</div>
				<div class="item">
					<img src="../assets/img/banner/discount-banner2.jpg">
				</div>
				<div class="item">
					<img src="../assets/img/banner/discount-banner2.jpg">
				</div>
			</div>
			<!-- Controls -->
			<a class="left carousel-control" href="#carousel-example-generic" role="button" data-slide="prev">
				<span class="glyphicon glyphicon-chevron-left" aria-hidden="true"></span>
				<span class="sr-only">Previous</span>
			</a>
			<a class="right carousel-control" href="#carousel-example-generic" role="button" data-slide="next">
				<span class="glyphicon glyphicon-chevron-right" aria-hidden="true"></span>
				<span class="sr-only">Next</span>
			</a>
		</div>
	    <div data-am-widget="tabs" class="am-tabs-d2">
		    <div class="am-tabs-bd">
		        <div class="am-tab-panel am-active">
		            <ul class="new" id="discount">
		             	<li v-for="(item,index) in ret.list" :key="index" >
		             		<a v-on:click="getDicountById(item.id)">
		             			<div class="text">
		             				<h3 class="discount_title">{{item.title}}</h3>
		             			</div>
		             		</a>
		             	</li>	             			             	
		            </ul>
		        </div>
		    </div>
        </div>
	</div>
</template>
<script>
import "../assets/bootstrap/js/bootstrap"
export default {
    name : 'DiscountList' , 
    data(){
        return{
            YouHuiId:1,
            ret:[],
            list:[]
        }
    } , 
    created : function (){
        //获取到列表
        this.getList();
    } ,
    methods :{
        //获取列表
        getList : function (){
            fetch("/apis/wxWebApp/youhui/getYouHuiListByPage.action",{	//调后台接口
                method:"POST",
                body : JSON.stringify({
                    currentPage : 1
                })
			})
			.then(res => res.json())
			.then(data => {
                this.ret = data ;
                //console.log("获取列表"+data);
            })
        } ,
        //根据优惠讯息ID获取优惠讯息
		getDicountById:function(id){
      		//console.log('传过来的ID'+id)
			this.$router.push({
				path : '/discount/' + id
			})
        }

    }
}
</script>
<style scoped>
@import url("../assets/bootstrap/css/bootstrap.css");
.carousel-indicators{
	bottom: -5px
}
.discount_title { 
  margin-top: -5px; 
  margin-bottom: 0;
  color: #333; 
  width: 100%;  
  font-size: 1.6rem;
  line-height: 24px;
}
</style>



