<template>
    <div id="ProductDetail">
		<div class="zhanshi">
			<div class="zhan-pic"><img src="../assets/img/product/e.jpg" width="240"></div>
			<div class="zhanshi_h2">
				<h2>产品介绍</h2>
				<h2 class="buy">立即购买</h2>				
			</div>
			<div style="clear: both"></div>
			<ul class="pro-jieshao">
				<li>名称：{{ret.productName}}</li>
				<li>类别：{{ret.productCategoryName}}</li>
				<li>白酒香型：{{ ret.liquorFlavorTypes}}</li>
				<li>酒精度：{{ret.alcoholic}}度</li>
				<li>单只容量：{{ret.volume}}ml</li>
				<li>价格：¥{{ret.productSalePrice}}</li>
			</ul>
		</div>
		<div class="product-text">
			{{ret.productDetail}}
		</div>
	</div>
</template>
<script>
export default {
	name:'ProductDetail',
	data(){
		return{
			ret:[]
		}
	},
	created:function(){
		this.loadData()
	},
	methods:{
		loadData:function(){
			var productId = this.$route.params.id;	//获取id
			fetch("/apis/wxWebApp/product/getProductById.action",{
				method:'POST',
				body:JSON.stringify({
					productID : productId
				})
			})
			.then(res => res.json())
			.then(data => {
				//console.log(data);
				this.ret = data
			})
		}
	}
}
</script>


