<template>
    <div>
        <div id="login"></div>
        <div class="login_bg">
            <div id="logo">
                <img src="../assets/img/logo.png" alt=""/>
            </div>
            <div class="form">
                <div class="userName">
                    <span>用户名：</span>
                    <input type="text" placeholder="请输入用户名" v-model="loginName"/>
                </div>
                <div class="passWord">
                    <span>密&nbsp;&nbsp;&nbsp;码：</span>
                    <input type="password" placeholder="请输入密码" v-model="loginPwd"/>
                </div>
                <div class="choose_box">
                    <div>
                        <input type="checkbox" checked="checked"/>
                        <span>记住密码</span>
                    </div>
                    <a>忘记密码</a>
                </div>
                <button class="login_btn" v-on:click='login'>登&nbsp;&nbsp;录</button>
            </div>
        </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
    name: 'Login',
    data(){
        return{
            loginName:'',
            loginPwd:''
        }
    },
    watch:{ //数据双向绑定
        loginName:function(newText){
            this.loginName = newText
        },
        loginPwd:function(newText){
            this.loginPwd = newText
        }
    },
    methods:{
        login:function(){
            if(this.loginName == ''){
                Toast({
                    message: '用户名不能为空！',
                    position: 'middle',
                    duration: 3000
                });
                return false;
            }
            if(this.loginPwd == ''){
                Toast({
                    message: '密码不能为空！',
                    position: 'middle',
                    duration: 3000
                });
                return false;
            }
            fetch("/apis/wxWebApp/wechat/userLoginPost.action",{
                method:'POST',
                body:JSON.stringify({ "loginName":this.loginName,"loginPwd":this.loginPwd})
            })
            .then(res => res.json())
            .then(data => {
                //console.log(data);
                //登录成功
                if(data.ok){
                    this.$router.push({path: "/home"});
                }else{ //登录失败
                    Toast({
                        message: data.retMsg,
                        position: 'middle',
                        duration: 3000
                    });
                }
            })
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "../assets/css/login.css"
</style>



