import Vue from 'vue'
import App from './App'

//引入mint-ui
import Mint from 'mint-ui';
import 'mint-ui/lib/style.css'; 

Vue.use(Mint);

import VueRouter from 'vue-router'
import routers from './router'

Vue.use(VueRouter)
Vue.config.productionTip = false

const router = new VueRouter({
  mode: 'history',
  routes: routers
})
/* eslint-disable no-new */
var vm = new Vue({
  el: '#app',
  router , 
  components: { App },
  template: '<App/>'
})


