<template>
    <div>
		<!--图片轮播-->
        <div id="carousel-example-generic" class="carousel slide" data-ride="carousel">
			<!-- Indicators -->
			<ol class="carousel-indicators">
				<li data-target="#carousel-example-generic" data-slide-to="0" class="active"></li>
				<li data-target="#carousel-example-generic" data-slide-to="1"></li>
				<li data-target="#carousel-example-generic" data-slide-to="2"></li>
			</ol>
			<!-- Wrapper for slides -->
			<div class="carousel-inner" role="listbox">
				<div class="item active">
					<img src="../assets/img/banner/banner1.jpg">
				</div>
				<div class="item">
					<img src="../assets/img/banner/banner2.jpg">
				</div>
				<div class="item">
					<img src="../assets/img/banner/banner3.jpg">
				</div>
			</div>
			<!-- Controls -->
			<a class="left carousel-control" href="#carousel-example-generic" role="button" data-slide="prev">
				<span class="glyphicon glyphicon-chevron-left" aria-hidden="true"></span>
				<span class="sr-only">Previous</span>
			</a>
			<a class="right carousel-control" href="#carousel-example-generic" role="button" data-slide="next">
				<span class="glyphicon glyphicon-chevron-right" aria-hidden="true"></span>
				<span class="sr-only">Next</span>
			</a>
		</div>
		<div class="dh am-cf">
		  	<ul>
		    	<router-link to="/home">
		    		<li class="h200"><i class="iconfont"><img src="../assets/img/about.png" /></i><span>关于我们</span></li>
		    	</router-link>
		    	<router-link to="/news">
		    		<li><i class="iconfont"><img src="../assets/img/new.png" /></i><span>新闻资讯</span></li>
		    	</router-link>
		    	<router-link to="/categoryProduct">
		    		<li><i class="iconfont"><img src="../assets/img/14.png" /></i><span>产品中心</span></li>
		    	</router-link>
		    	<router-link to="/discountList">
		    		<li><i class="iconfont"><img src="../assets/img/13.png" /></i><span>优惠讯息</span></li>
		    	</router-link>
		    	<router-link to="">
		    		<li><i class="iconfont"><img src="../assets/img/contact.png" /></i><span>联系我们</span></li>
		    	</router-link>
		  	</ul>
		</div>

		<div class="jianju"></div>

		<div data-am-widget="titlebar" class="am-titlebar am-titlebar-default" >
		    <h2 class="am-titlebar-title">明星产品</h2>
		    <nav class="am-titlebar-nav"><router-link to="/categoryProduct">更多 &raquo;</router-link></nav>
		</div>
	  	<ul id="products" data-am-widget="gallery" class="am-gallery am-avg-sm-2-2 am-avg-md-3 am-avg-lg-4 am-gallery-imgbordered" data-am-gallery="{  }" >
	      	<li v-for="(item,index) in ret.mxProducts" :key="index">
	        	<div class="am-gallery-item" v-on:click="getProductById(item.id)">
					<!-- 根据id进入详情 -->
	            	<!-- <router-link :to="{path:'/ProductDetail/'+item.id}"> -->
	              		<img v-bind:src="host + '/' + item.productImage" />
	                	<h3 class="am-gallery-title">{{item.productName}}</h3>
	            	<!-- </router-link> -->
	        	</div>
	      	</li>
	  	</ul>

	  	<div class="jianju"></div>

	  	<div data-am-widget="list_news" class="am-list-news am-list-news-default" >
	      	<div data-am-widget="titlebar" class="am-titlebar am-titlebar-default" >
		    	<h2 class="am-titlebar-title">优惠讯息</h2>
		    	<nav class="am-titlebar-nav"><router-link to="/discountList">更多 &raquo;</router-link></nav>
			</div>
		  	<div class="am-list-news-bd">
		  		<ul class="am-list">
		      		<li v-for="(item,index) in ret.yhNews" :key="index" v-on:click="getDicountById(item.id)" class="am-g am-list-item-dated">
		          		<a class="am-list-item-h">{{item.title}}</a>
		      		</li>
		  		</ul>
		  	</div>
      	</div>
	</div>
</template>
<script>
import "../assets/bootstrap/js/bootstrap"
export default {
	name:'Home',
	data(){
		return{
			ret:[],
			host:'/apis/wxWebApp/upload'	//调取图片
		}
	},
	created:function(){
		this.loadData();
	},
	methods:{
		loadData:function(){
			fetch("/apis/wxWebApp/wechat/loadIndexData.action",{	//调后台接口
				method:"GET",
			})
			.then(res => res.json())
			.then(data => {
				//console.log(data);
				this.ret = data
			})
		},
		//跟据产品ID获取产品信息
    	getProductById:function(id){
      		//console.log('传过来的ID'+id)
      		//this.$router.push({ path: "/productDetail" , params:{ id : 1} });
			this.$router.push({
				path : '/productDetail/' + id
			})
		},

		//根据优惠讯息ID获取优惠讯息
		getDicountById:function(id){
      		//console.log('传过来的ID'+id)
      		//this.$router.push({ path: "/productDetail" , params:{ id : 1} });
			this.$router.push({
				path : '/discount/' + id
			})
		}
	}
}
</script>
<style scoped>
@import url("../assets/bootstrap/css/bootstrap.css");
.carousel-indicators{
	bottom: -5px
}
</style>



