<template>
    <div>
	    <div class="newcon-pic">
			<img src="../assets/img/news/news2.jpg">
			<p class="newcon-tit">{{info.title}}</p>
			<p class="newcon-time">活动时间：{{dateFormat(info.beginTime)}}-{{dateFormat(info.endTime)}}</p>
	    </div>
	    <div class="jianju"></div>
	    <div class="newcon">
	    	{{info.content}}
	    </div>
	</div>
</template>

<script>
export default {
    name:'Discount',
	data(){
		return{
			info:[]
		}
	},
	created:function(){
        this.loadData();
        //this.dateFormat()
	},
	methods:{
		loadData:function(){
			var YouHuiId = this.$route.params.id;	//获取id
			fetch("/apis/wxWebApp/youhui/findYouHuiModelById.action",{
				method:'POST',
				body:JSON.stringify({
					YouHuiId : YouHuiId
				})
			})
			.then(res => res.json())
			.then(data => {
				//console.log(data);
				this.info = data
			})
        },
        dateFormat:function(time) {
			var date = new Date(time);
			var year = date.getFullYear();	//年
			var month = date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;	//月
			var day = date.getDate()<10 ? "0"+date.getDate() : date.getDate();	//日
			//var hours = date.getHours()<10 ? "0"+date.getHours() : date.getHours();	//时
			//var minutes = date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();	//分
			//var seconds = date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();	//秒
			return year + "年" + month + "月" + day + "日";	//拼接
		},
	}
}
</script>


