<template>
  <div id="app">
    <!-- <myHeader v-show="!(path ==='/')"></myHeader> -->
    <myHeader></myHeader>
    <router-view></router-view>
    <!-- <myFooter v-show="!(path ==='/')"></myFooter> -->
    <myFooter></myFooter>
  </div>
</template>

<script>

import "./assets/js/jquery.min.js"
import "./assets/amaze/amazeui.min.js"
import myHeader from "./components/common/Header"
import myFooter from "./components/common/Footer"
export default {
  name: 'App',
  data(){
    return{
      path:''
    }
  },
  components:{
    myHeader,
    myFooter
  },
  // 判断路由
  mounted() {
    this.path = this.$route.path;
    //console.log(this.$route.path)
  },
  watch:{
    $route(to,from){
      this.path = to.path
    }
  }
}
</script>

<style>

@import "./assets/amaze/amazeui.min.css";
@import "./assets/css/app.css";
</style>



