<template>
    <div id="categoryProduct">
        <div class="content-list">
	    	<ul class="list-left" id="tab">
                <!-- <li class="current"><a href="javascript:;">白酒</a></li> -->
                <li :class="{ current:index==isActive }" v-on:click="getProductByCategoryId(item.id) ; changeValue(index)" v-for="(item ,index) in productCategory" :key="index">{{item.categoryName}}</li>
	    	</ul>
	    	<div class="list-right" id="content">
	    		<ul data-am-widget="gallery" class="am-gallery am-avg-sm-2 am-avg-md-2 am-avg-lg-4 am-gallery-default am-no-layout">
					<li v-for="(item,index) in products" :key="index">
				        <div class="am-gallery-item">
				            <a v-on:click="toProductDetail(item.id)" class="">
				              	<!-- <img src="images/product/k.jpg"> -->
                                <img v-bind:src="host + '/'+item.productImage" alt="">
				                <h3 class="am-gallery-title">{{ item.productName}}</h3>
				            </a>
				        </div>
				    </li>				    			     
				</ul>
	    	</div>
	    </div>
    </div>
</template>

<script>
export default {
    name : 'CategoryProduct' , 
    data(){
        return{
            categoryId : 1 ,
            productCategory : [] , 
            products : [] , 
            host:'/apis/wxWebApp/upload' ,	//调取图片
            isActive:false
        }
    } , 
    created : function (){
        //获取到所有的产品类别
        this.getProductCategories()
        //跟据产品类别ID
        this.getProductByCategoryId(this.categoryId)
    } ,
    methods :{
        //转到商品详情页
        toProductDetail:function(id){
            //console.log('传过来的ID'+id)
      		//this.$router.push({ path: "/productDetail" , params:{ id : 1} });
			this.$router.push(
			{
				path : '/productDetail/'+id
			})
        },
        changeValue(index){
            this.isActive=index;
        } ,
        //获取所有的产品类别
        getProductCategories : function (){
            fetch("/apis/wxWebApp/product/getAllProductCategory.action",{	//调后台接口
				method:"POST",
			})
			.then(res => res.json())
			.then(data => {
                this.productCategory = data ;
                //console.log('全部类别'+data);
                if(this.productCategory.length >= 0 ){
                    this.categoryId = this.productCategory[0].id;
                }
            })
            return this.categoryId 
        } , 
        //跟据产品类别ID获取产品
        getProductByCategoryId : function (categoryId){
            //console.log("跟据产品类别获取产品："+categoryId)
            fetch("/apis/wxWebApp/product/getAllProductListByCategoryId.action",{	//调后台接口
                method:"POST",
                body :JSON.stringify({
                    ProductCategoryID : categoryId
                })
			})
			.then(res => res.json())
			.then(data => {
				//console.log("获取产品"+data) ;
				this.products = data
            })
            
        } 
    }
}
</script>


